@charset "UTF-8";

.user-info {
  padding: 15PX;
  font-size: 15PX;
  color: #313131;
}

.login-no {
  overflow: hidden;
  margin: 8PX 9PX;
}

.login-no .login {
  float: right;
  height: 24PX;
  line-height: 24PX;
  padding-left: 34PX;
  position: relative;
}

.login-no .login:before {
  width: 24PX;
  height: 24PX;
  content: '';
  position: absolute;
  left: 0;
  top: 0;
}

.login-no .login {
  float: left;
}

.login-no .login:before {
  background: url(data:image/png;base64,) no-repeat left center;
  background-size: 24PX 24PX;
}

/*已登录*/

.login-yes {
  height: 100%;
  background: url(data:image/png;base64,) no-repeat right center;
  background-size: 6PX 10PX;
  overflow: hidden;
  position: relative;
}

.login-yes .avertar {
  width: 40PX;
  height: 40PX;
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 40PX 40PX;
  border-radius: 20PX;
  overflow: hidden;
  float: left;
}

.login-yes .info {
  margin-left: 0.21333rem;
  overflow: hidden;
  float: left;
}

.login-yes:after {
  display: block;
  background: url(data:image/png;base64,) no-repeat center right;
  background-size: 7PX 7PX;
}

