.header .page-cover {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.4);
  z-index: 7;
}

.header .header-bar {
  border-bottom: 0.02133rem solid #e8e8e8;
}

.header .header-bar.fix-header {
  width: 100%;
  background-color: rgba(255, 255, 255, 0.95);
  position: fixed;
  top: 0;
  left: 0;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  box-shadow: 0 0 0.08533rem rgba(0, 0, 0, 0.25);
  z-index: 6;
}

.header .header-bar.no-fix {
  width: 100%;
  background-color: #fff;
  overflow: hidden;
}

.header .header-bar.show {
  -webkit-transform: translateX(9.81333rem);
          transform: translateX(9.81333rem);
}

.header .nv-toolbar {
  width: 100%;
  height: 2.13333rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.header .nv-toolbar .toolbar-nav {
  width: 1.74933rem;
  height: 2.13333rem;
  position: absolute;
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 0.81067rem 0.68267rem;
  margin: 0;
  z-index: 1;
  top: 0;
  left: 0;
}

.header .nv-toolbar .title-name {
  display: block;
  text-align: center;
  height: 100%;
  line-height: 2.13333rem;
  font-size: 0.81067rem;
  width: 100%;
  position: relative;
  z-index: 0;
}

.header .nv-toolbar .num {
  background-color: #80bd01;
  color: #fff;
  width: 0.42667rem;
  height: 0.42667rem;
  line-height: 0.42667rem;
  vertical-align: middle;
  text-align: center;
  border-radius: 50%;
  position: absolute;
  right: 0.21333rem;
  top: 0.21333rem;
  z-index: 10;
}

.header .nv-toolbar .add-icon,
.header .nv-toolbar a-link {
  color: #42b983;
  position: absolute;
  right: 0.32rem;
  top: 0.32rem;
  z-index: 10;
  font-size: 0.81067rem;
  padding: 0.10667rem 0.32rem;
  border-radius: 0.10667rem;
}

